<template>
  <div class="home pd-top-bottom">
    <banner
      :data="banner"
      collectionName="banner"
    />
    <div class="list">
      <uc-cell 
        :key="item._id"
        v-for="(item,index) of home"
        :index="index" 
        :data="item" 
        :to="{name:'detail',params:{_id:item._id}, query:{collectionName:'home'}}"
      />
    </div>
  </div>
</template>

<script>
  import Banner from '../components/swiper/index.vue'
  import UcCell from '../components/uc-cell.vue'
  // import axios from '../plugins/axios.js';
  export default {
    name:'home',
    props:{},
    data(){
      return {
        banner:[],
        home:[]
      }
    },
    components:{Banner,UcCell},
    mounted(){
      this.$axios({
        url:'/api/goods/home',
        params:{_limit:20}
      }).then(
        res=>this.home=res.data.data
      )
      
      this.$axios({
        url:'/api/goods/banner',
        params:{_limit:3}
      }).then(
        res=>this.banner=res.data.data
      )
    },
    
    updated(){
      console.log('updated')
    },
    
    methods:{}
  }
</script>

<style>
  .home{}
  .list{padding: 0 .3rem;}
</style>
